.toolContainer {
     display: flex;
    flex-wrap: wrap;
    justify-content: center;
    height: 100%;
    border: solid 3px black;
    border-radius: 7px;
    width: 900px;
    margin-left: 15%;
    margin-right: 15%;
    margin-top: 30px;
    text-align: center;
    background-color: rgb(88, 86, 86);
    color: greenyellow; 
}

.navigation {
     border: solid 1px black;
    height: 100px;
    width: 100px;
    border-radius: 7px;
    margin-left: 20px;
    margin-top: 20px;
    background-image: url(https://ceblog.s3.amazonaws.com/wp-content/uploads/2013/07/navigation.jpg);
    background-size: cover; 
}
.widgetContainer {
     margin-top: 78%;
    border: solid 1px black;
    background-color: black;
    color: greenyellow;
    height: 20px;
    width: 100px;
    border-bottom-left-radius: 7px;
    border-bottom-right-radius: 7px; 
    
}

.weather {
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    height: 100px;
    width: 100px;
    border-radius: 7px;
    margin-left: 20px;
    margin-top: 20px;
    border: solid 1px black;

}
.calculator {
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    height: 100px;
    width: 100px;
    border-radius: 7px;
    margin-left: 20px;
    margin-top: 20px;
    border: solid 1px black;
}
.translator {
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    height: 100px;
    width: 100px;
    border-radius: 7px;
    margin-left: 20px;
    margin-top: 20px;
    border: solid 1px black;
} 
.navigation:hover {
    height: 200px;
    width: 200px;
  
}
.weather:hover {
    height: 200px;
    width: 200px;
}

.calculator:hover {
    height: 200px;
    width: 200px;
}

.calender:hover {
    height: 200px;
    width: 200px;
}


@media only screen and (min-width: 320px) and (max-width: 600px) {
    .toolContainer {
      width: 100%;
       padding-left: 23px;
      flex-direction: column-reverse; 
    }
    .boxes {
      width: 100%;
    }
  }
